@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;500&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Space Grotesk', sans-serif;
}

body,
p {
  margin: 0;
}

main {
  /* background-color: rgba(0, 0, 0, 0.719); */
  display: flex;
  flex-direction: column;
  height: 100%;
  background-repeat: no-repeat;
  background-image: url(/interactive-card-details-react/static/media/bg-main-mobile.caa46894.png);
  background-size: contain;
}

.card-container {
  height: auto;
  margin: auto 1rem;
  display: flex;
  flex-flow: column-reverse wrap;
  justify-content: space-between;
  padding-top: 2rem;
}

.form-container {
  margin: 0px auto;
  padding: 25px;
  display: flex;
  margin-top: 25px;
  max-width: 25rem;
}

@media (min-width: 750px) {
  main {
    flex-direction: row;
    background-image: url(/interactive-card-details-react/static/media/desktop.806e7aa7.png);

    height: 100vh;
  }

  .card-container {
    height: 100%;
    width: 50%;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    grid-gap: 2.1rem;
    gap: 2.1rem;
    font-size: 1em;
  }


  .form-container {
    margin: auto;
    padding: 25px;
    display: flex;
    max-width: 25rem;
    margin-left: 5rem;
  }
}
input {
    outline: none;
    width: 100%;
    height: 2.7rem;
    box-sizing: border-box;
    border-radius: 9px;
    border: 1px solid hsl(270, 3%, 87%);
    font-family: inherit;
    font-size: 1rem;
    padding: 0rem 1rem;
}

input:focus {
    outline: none;
    border-color: hsl(278, 68%, 11%);
}

::-webkit-input-placeholder {
    color: hsl(270, 3%, 87%);
}

::placeholder {
    color: hsl(270, 3%, 87%);
}

label {
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 0.5rem;
    letter-spacing: 0.1rem;
    text-transform: uppercase;
    font-size: 0.8rem;
}

.Form_error__2ZwuW,
.Form_error__2ZwuW:focus {
    border-color: hsl(0, 100%, 66%);
}



.Form_form__1zRvZ,
form div {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    grid-gap: 0rem 0.7rem;
    gap: 0rem 0.7rem;
}



button {
    height: 3rem;
    width: 100%;
    background-color: hsl(278, 68%, 11%);
    color: white;
    border-radius: 7px;
    border: none;
}

button:hover {
    background-color: hsla(278, 68%, 11%, 0.9);
}

div:nth-child(3) {
    flex: 1 1;
}

div:nth-child(3) input {
    flex: 1 1;
}

div:nth-child(4) {
    flex: 1 1;
}
.Error_error__2Ym5D {
    font-size: 0.7em;
    width: 100%;
    color: hsl(0, 100%, 66%);
    height: 0.8em;
    margin: 0.3rem 0rem;
    padding-bottom: 1.5em;
}

.Error_noError__2XGTI {
    font-size: 0.7em;
    width: 100%;
    visibility: hidden;
    height: 0.8em;
    margin: 0.3rem 0rem;
    padding-bottom: 1.5em;
}
.Card_card-front__EstlR {
    align-self: flex-start;
    background-image: url(/interactive-card-details-react/static/media/bg-card-front.8153e2ca.png);
    margin-top: -4rem;
}

.Card_card-back__3TuZU {
    align-self: flex-end;
    background-image: url(/interactive-card-details-react/static/media/bg-card-back.23f68fa3.png);
}

.Card_card-back__3TuZU,
.Card_card-front__EstlR {
    background-color: transparent;
    box-shadow: 1rem 3rem 7rem rgba(0, 0, 0, 0.397);
    display: flex;
    flex-wrap: wrap;
    color: white;
    background-size: contain;
    background-repeat: no-repeat;
    aspect-ratio: 447/245;
    width: 18em;
    padding: 1.2em;
    border-radius: 10px;
}

.Card_card-back__3TuZU p {
    /* background-color: aqua; */
    margin-top: 4.7em;
    margin-right: 1.7em;
    width: 100%;
    text-align: end;
    font-size: 0.7em;
}

.Card_card-front__EstlR img {
    height: 2em;
}


.Card_card-front__EstlR p {
    width: 100%;
    text-transform: uppercase;
    align-self: flex-end;
    letter-spacing: 0.2em;
}


.Card_card-front__EstlR p:nth-last-child(2) {
    flex: 2 1;
    text-align: start;
    font-size: 0.6em;
}

.Card_card-front__EstlR p:last-child {
    text-align: end;
    flex: 1 1;
    font-size: 0.6em;
}

@media (min-width: 750px) {

    .Card_card-back__3TuZU,
    .Card_card-front__EstlR {
        font-size: 1.5em;
        border-radius: 15px;
    }

    .Card_card-front__EstlR {
        align-self: center;
    }


}
.Complete_confirmed__1KZm- {
    max-width: 25rem;
    padding: auto;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0rem auto;
}

.Complete_confirmed__1KZm- p {
    font-size: 1.1rem;
    margin-bottom: 2.5rem;
    color: hsl(279, 6%, 55%);
}

.Complete_confirmed__1KZm- button {
    width: 100%
}

@media (min-width: 750px) {
    .Complete_confirmed__1KZm- {
        min-width: 25rem;
    }
}
